
import Vue from 'vue'
import { Component, Prop } from 'vue-property-decorator'

@Component
export default class ToolbarItem extends Vue {
    @Prop() icon!: string
    @Prop() title!: string
    @Prop() color!: string

    clicked(): void {
        this.$emit('click')
    }

    get size(): any {
        const size = { xs: '', sm: 'medium', md: '', lg: 'large', xl: 'x-large' }[this.$vuetify.breakpoint.name]
        return size ? { [size]: true } : {}
    }
}
